<template>
    <tr v-for="(price, job) in jobs" :key="job">

       <td class="d-block d-md-none w-100 text-center">{{ job }}</td>
       <td class="d-block d-md-none w-100 text-center">{{ price }}</td>
       <td class="d-block d-md-none w-100 text-center" is="vue:price-to-calc"
         @change-calc="dataPreparation($event, job, price)">
       </td>

       <td class="d-none d-md-table-cell d-lg-none">{{ job }}</td>
       <td class="d-none d-md-table-cell d-lg-none">
         <div class="d-flex flex-column justify-content-center align-items-center">
           {{ price }}
           <price-to-calc class="d-none d-md-inline-block d-lg-none mt-2"
             @change-calc="dataPreparation($event, job, price)" />
         </div>
       </td>

       <td class="d-none d-lg-table-cell">{{ job }}</td>
       <td class="d-none d-lg-table-cell">{{ price }}</td>
       <td class="d-none d-lg-table-cell" is="vue:price-to-calc"
         @change-calc="dataPreparation($event, job, price)">
       </td>

   </tr>
</template>
<script>
import PriceToCalc from '@/components/PriceToCalc.vue'
export default {
  name: 'JobsTable',
  props: ['jobs'],
  emits: ['changeCalcData'],
  components: {
    'price-to-calc': PriceToCalc
  },
  methods: {
    dataPreparation (action, currentJob, currentPrice) {
      this.$emit('changeCalcData', {
        name: currentJob,
        number: action === 'add' ? 1 : -1,
        price: currentPrice
      })
    }
  }
}
</script>
