<template>
  <div>
    <slot></slot>
    <button
      type="button"
      class="btn btn-lg btn-success rounded me-2"
      @click="runCurrentEmit('add')"
      >
      +
    </button>
    {{ counter }}
    <button
      :disabled="!counter"
      type="button"
      class="btn btn-lg btn-primary rounded ms-2"
      @click="runCurrentEmit('remove')"
      >
      &ndash;
    </button>
  </div>
</template>
<script>
export default {
  data () {
    return {
      counter: 0
    }
  },
  emits: ['changeCalc'],
  methods: {
    runCurrentEmit (action) {
      this.$emit('changeCalc', action)
      this.counter += action === 'add' ? 1 : -1
    }
  }
}
</script>
