<template>
  <div>
    <template v-if="showCardSide">
      <img :src="good.imgURL" class="card-img-top mt-3" alt="...">
      <div class="card-body pt-2">
        <h5 class="card-title">{{ good.brandName }}</h5>
        <h6 class="card-subtitle mb-2 text-muted">{{ good.name }}</h6>
        <p class="card-text div-bottom">{{ good.rating }} <card-rating :rating="good.rating" /></p>
        <h5 class="card-title div-bottom">Цена: {{ good.price }} &#8381;</h5>
      </div>
    </template>
    <template v-else>
      <ul class="list-group list-group-flush">
        <li class="list-group-item">Тип - {{ good.type }}</li>
        <li v-for="(property, name) in good.properties" :key="name" class="list-group-item">{{ name }}{{ property }}</li>
      </ul>
      <a :href="good.infoURL" class="card-link" target="_blank">Подробнее о товаре</a>
    </template>
    <div>
      <price-to-calc @change-calc="dataPreparation" class="d-flex justify-content-evenly align-items-center mb-4">
          <button class="btn btn-lg btn-info me-5"
          type="button"
          @click="showCardSide = !showCardSide"
          >
            <template v-if="showCardSide">?</template>
            <template v-else>X</template>
          </button>
        </price-to-calc>
    </div>
  </div>
</template>
<script>
import PriceToCalc from '@/components/PriceToCalc.vue'
import StarsRating from '@/components/svg/StarsRating.vue'
export default {
  name: 'CardProduct',
  props: ['good'],
  data () {
    return {
      showCardSide: true
    }
  },
  emits: ['changeCalcData'],
  components: {
    'card-rating': StarsRating,
    'price-to-calc': PriceToCalc
  },
  methods: {
    dataPreparation (action) {
      this.$emit('changeCalcData', {
        name: `${this.good.brandName} ${this.good.name}`,
        number: action === 'add' ? 1 : -1,
        price: this.good.price
      })
    }
  }
}
</script>
